	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Libertarian Democrat","date":"25 Sep 2005","slug":"/2005-09-25-libertarian-democrat/","tags":"Y2005"},"html":"<p>A lot of people are trying the <a href=\"http://www.okcupid.com/politics\">the politics test</a>. The German <a href=\"http://www.wahlomat.de/\">Wahl-O-Mat</a> called me a <a href=\"http://www.papascott.de/archives/2005/08/26/wahl-o-mat/\">Green Neo-Liberal</a>, this test calls me a Libertarian Democrat.</p>\n<blockquote><p>You are a Social Liberal (85% permissive) and an Economic Conservative (66% permissive) You are best described as a: <b>Libertarian</b>.  You exhibit a very well-developed sense of Right and Wrong and believe in economic fairness.</p></blockquote>\n<table id=\"thetable\" name=\"thetable\" background=\"http://is2.okcupid.com/graphics/politics/chart_political.gif\" border=\"0\" cellpadding=\"0\" cellspacing=\"0\" height=\"375\" width=\"375\">\n<tbody>\n<tr height=\"106\">\n<td width=\"299\"></td>\n<td width=\"75\"></td>\n</tr>\n<tr height=\"268\">\n<td width=\"299\"></td>\n<td align=\"left\" valign=\"top\" width=\"75\"><img src=\"http://is2.okcupid.com/graphics/politics_you.gif\" border=\"0\" /></td>\n</tr>\n</tbody>\n</table>\n<p> </p>\n<p>At least I'm consistent, even across borders.</p>"},"next":{"fields":{"title":"Rocket Science","date":"24 Sep 2005","slug":"/2005-09-24-rocket-science/"}},"previous":{"fields":{"title":"Lie To Me","date":"25 Sep 2005","slug":"/2005-09-25-lie-to-me/"}}},{"node":{"fields":{"title":"Rocket Science","date":"24 Sep 2005","slug":"/2005-09-24-rocket-science/","tags":"Y2005"},"html":"<p>When you build a rocket from a cardboard box, you first need to draw the blueprints.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/46018967/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/31/46018967_5c8837df26.jpg\" width=\"500\" height=\"375\" alt=\"IMG_0421\" /></a></p>\n<p><a href=\"http://www.flickr.com/photos/papascott/46018640/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/28/46018640_9d199ae63d.jpg\" width=\"375\" height=\"500\" alt=\"IMG_0419\" /></a></p>\n<p><a href=\"http://www.flickr.com/photos/papascott/46018814/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/26/46018814_397cf55c9e.jpg\" width=\"375\" height=\"500\" alt=\"IMG_0420\" /></a></p>"},"next":{"fields":{"title":"Good Tickets","date":"24 Sep 2005","slug":"/2005-09-24-good-tickets/"}},"previous":{"fields":{"title":"Libertarian Democrat","date":"25 Sep 2005","slug":"/2005-09-25-libertarian-democrat/"}}},{"node":{"fields":{"title":"Good Tickets","date":"24 Sep 2005","slug":"/2005-09-24-good-tickets/","tags":"Y2005"},"html":"<p>It turned out the <a href=\"http://www.papascott.de/archives/2005/08/09/bad-tickets/\" title=\"PapaScott &raquo; Blog Archive &raquo; Bad Tickets\">luxury lounge HSV tickets</a> for the match against Bayern weren't so bad after all. We did have seats outside, so we didn't have to stay in the lounge. And the home team <a href=\"http://sportsillustrated.cnn.com/2005/soccer/wires/09/24/2080.ap.eu.spt.soc.german.roundup/index.html\" title=\"SI.com - Hamburg ends Bayern's record streak with 2-0 win\">ended up winning</a>, beating Bayern for the first time since 1996.</p>\n<p>It was my first HSV match <a href=\"http://www.papascott.de/archives/2004/11/25/monkey-noises/\">since 1990</a>. I was very impressed by the atmosphere in the AOL Arena this time, and I'll probably go again (even without the luxury business lounge).</p>\n<p>Today's tickets were a gift for Mama's nephew, a huge Bayern fan (who was of course deeply disappointed by the result). Christopher and I were supposed to chaperone, but when Christopher turned out not to be interested, we had a <a href=\"http://lumma.de/eintrag.php?id=2107\" title=\"HSV olé [Lummaland - das Weblog]\">ticket left over</a>. <a href=\"http://frederikesophie.blogg.de/eintrag.php?id=226\" title=\"HSV-Mampf [Frederike Sophie]\">Rike</a> seemed to enjoy the buffet.</p>"},"next":{"fields":{"title":"MySQL Tip","date":"22 Sep 2005","slug":"/2005-09-22-mysql-tip/"}},"previous":{"fields":{"title":"Rocket Science","date":"24 Sep 2005","slug":"/2005-09-24-rocket-science/"}}}],"pathPrefix":"","first":false,"last":false,"index":278,"pageCount":1101}}

